@import url(https://fonts.googleapis.com/css2?family=Krona+One&display=swap);


h1, a, span, li , div {
  font-family: Arial, Geneva, Helvetica, sans-serif;
  text-decoration: none;
}

.App {
  text-align: center;
}

.page {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.content {
  position: relative;
  top: 4rem;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
}

section {
  width: 100%;
  height: 100%;
}

.page-format {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100%;
}

.page-content {
  width: 1250px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header-container {
  z-index: 999;
  position: fixed;
  width: 100%;
  background-color: black;
  color: white;
}

.header-content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem;
}

.menu-icon {
  color: white;
  font-size: 2rem;
}

.menu-icon:hover {
  -webkit-animation: burger 5s linear infinite;
          animation: burger 5s linear infinite;
}

@-webkit-keyframes burger {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

@keyframes burger {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

.dropdown {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: rgb(129, 129, 255, .98);
  padding: 0rem;
  height: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  text-align: center;
}

#header-menu {
  display: none;
  background-color: blue;
  position: fixed;
  z-index: 15;
  left: 0;
  top: 10rem;
}

.dropdown-container {
  position: fixed;
  top: -100%;
  z-index: 400;
  width: 100%;
  transition: all .7s ease-in-out;
  height: 100%;
  -webkit-animation: bounce 1s forwards;
          animation: bounce 1s forwards;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: cubic-bezier(.83,.32,.11,.9);
          animation-timing-function: cubic-bezier(.83,.32,.11,.9);
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

.emoji-icon {
  display: inline;
  display: initial;
  color: white;
  font-size: 5rem;
  position: absolute;
  right: 30%;
  top: -2rem;
}

.header-link {
  padding: .5rem 0rem;
  color: white;
  margin: .2rem;
  width: 100%;
  padding: 1.5rem;
  font-weight: bold;
  font-size: 4rem;
}

.header-link:hover {
  background-color: rgb(0,0,0,0.3);
  width: 25rem;
}

#header-menu:checked ~ .dropdown-container {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes bounce {
  from {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
  }
  to {
    -webkit-transform: translate3d(0,100%,0);
            transform: translate3d(0,100%,0);
  }
}

@keyframes bounce {
  from {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
  }
  to {
    -webkit-transform: translate3d(0,100%,0);
            transform: translate3d(0,100%,0);
  }
}
.about-content {
  color: black;
}

.about-profile-img {
  height: 20rem;
  width: 20rem;
  object-fit: cover;
}

.portfolio-content {
  color: white;
}

.portfolio-icon {
  color: white;
  font-size: 5rem;
  padding: 1rem;
}

.portfolio-dropdown {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: white;
  color: black;
  width: 9rem;
  padding: 1rem;
}

.portfolio-dropdown > span {
  padding-top: .5rem;
}

.portfolio-menu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  left: -11rem;
  transition: left .5s linear;
}

#portfolio-list {
  display: none;
}

#portfolio-list:checked ~ .portfolio-menu {
  left: 0rem;
  transition: left .5s linear;
}
.contact-content {
  color: white;
}
.resume-content {
  color: black;
}
.home-font {
  font-size: 5rem;
  font-weight: bold;
  color: white;
}

.home-content {
  color: white;
}
.checkbox {
  display: inline;
  display: initial;
  position: relative;
  z-index: 200;
  left: 10rem;
}

.home-page, .contact-page {
  background-color: black;
  -webkit-animation: returnLeft 1s linear;
          animation: returnLeft 1s linear;
  height: 100%;
  padding-top: 3rem;
  padding-bottom: 3rem;
  -webkit-animation: returnLeft 1s forwards;
          animation: returnLeft 1s forwards;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: cubic-bezier(.83,.32,.11,.9);
          animation-timing-function: cubic-bezier(.83,.32,.11,.9);
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.portfolio-page {
  background-color: black;
  height: 100%;
  padding-top: 3rem;
  padding-bottom: 3rem;
  -webkit-animation: returnRight 1s forwards;
          animation: returnRight 1s forwards;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: cubic-bezier(.83,.32,.11,.9);
          animation-timing-function: cubic-bezier(.83,.32,.11,.9);
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.about-page, .resume-page {
  background-color: white;
  -webkit-animation: returnLeft 1s linear;
          animation: returnLeft 1s linear;
  height: 100%;
  padding-top: 3rem;
  padding-bottom: 3rem;
  -webkit-animation: returnLeft 1s forwards;
          animation: returnLeft 1s forwards;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: cubic-bezier(.83,.32,.11,.9);
          animation-timing-function: cubic-bezier(.83,.32,.11,.9);
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes returnLeft {
  from {
    -webkit-transform: translate3d(100%,0,0);
            transform: translate3d(100%,0,0)
  }
  to {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0)
  }
}

@keyframes returnLeft {
  from {
    -webkit-transform: translate3d(100%,0,0);
            transform: translate3d(100%,0,0)
  }
  to {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0)
  }
}

@-webkit-keyframes returnRight {
  from {
    -webkit-transform: translate3d(-100%,0,0);
            transform: translate3d(-100%,0,0)
  }
  to {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0)
  }
}

@keyframes returnRight {
  from {
    -webkit-transform: translate3d(-100%,0,0);
            transform: translate3d(-100%,0,0)
  }
  to {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0)
  }
}

